.background{
    background:url(https://codingapple.com/wp-content/uploads/2020/05/background.jpg)no-repeat center center;
    background-size: cover;
    color: #fff;
}

.my_alert{
    width: 100%;
    max-width: 500px;
    border-radius: 10px;
    margin: auto;
    background: #eeee;
    padding: 15px;
    margin-top: 100px;

    animation: 반짝 .5s infinite ;

    
    

    p{margin: 0;}
}

@keyframes 반짝{
    from {
        background: red;
      }
    
      to {
        background: yellow;
     }
}